// =============================================================================
// Webhook Config Store
// =============================================================================
// Database-backed store for webhook configurations
// Migrated from in-memory to database for production readiness

import type { WebhookConfig } from './webhook-delivery';
import * as webhookDb from '@/db/webhook-configs';

/**
 * Get all webhook configurations
 */
export async function getWebhookConfigs(filters?: {
  apiKeyId?: string;
  endpointId?: string;
  enabled?: boolean;
}): Promise<WebhookConfig[]> {
  return webhookDb.getWebhookConfigs(filters);
}

/**
 * Get webhook configuration by ID
 */
export async function getWebhookConfig(id: string): Promise<WebhookConfig | undefined> {
  const config = await webhookDb.getWebhookConfig(id);
  return config || undefined;
}

/**
 * Create or update webhook configuration
 */
export async function setWebhookConfig(config: WebhookConfig & { apiKeyId: string }): Promise<WebhookConfig> {
  const existing = await webhookDb.getWebhookConfig(config.id);
  if (existing) {
    const updated = await webhookDb.updateWebhookConfig(config.id, {
      url: config.url,
      events: config.events,
      enabled: config.enabled,
    });
    return updated || config;
  } else {
    return webhookDb.createWebhookConfig(config);
  }
}

/**
 * Delete webhook configuration
 */
export async function deleteWebhookConfig(id: string): Promise<boolean> {
  return webhookDb.deleteWebhookConfig(id);
}

