// =============================================================================
// Webhook Management API
// =============================================================================
// DELETE /api/v1/webhooks/[webhookId] - Delete webhook config
// PATCH /api/v1/webhooks/[webhookId] - Update webhook config

import { NextRequest, NextResponse } from 'next/server';
import { requireAuth } from '@/lib/auth';
import { extractRequestContext, wrapResponse, createErrorResponse } from '@/lib/request-logging';
import { getWebhookConfig, setWebhookConfig, deleteWebhookConfig } from '@/lib/webhook-config-store';
import type { WebhookEventType } from '@/lib/webhook-delivery';

/**
 * DELETE /api/v1/webhooks/[webhookId]
 * 
 * Delete webhook configuration
 */
export async function DELETE(
  request: NextRequest,
  { params }: { params: Promise<{ webhookId: string }> }
) {
  const { webhookId } = await params; // Next.js 15+ requires awaiting params
  const ctx = await extractRequestContext(request, `/api/v1/webhooks/${webhookId}`);
  
  try {
    // Require authentication
    const authResult = await requireAuth(request, 'user');
    if ('response' in authResult) {
      return wrapResponse(authResult.response, ctx);
    }
    
    ctx.apiKeyId = authResult.apiKey.id;
    
    const config = await getWebhookConfig(webhookId);
    if (!config) {
      return NextResponse.json(
        {
          error: 'Webhook not found',
          code: 'NOT_FOUND',
        },
        { status: 404 }
      );
    }
    
    await deleteWebhookConfig(webhookId);
    
    return NextResponse.json({
      success: true,
      message: 'Webhook deleted',
    });
  } catch (error) {
    return createErrorResponse(
      ctx,
      error instanceof Error ? error : new Error('Internal server error'),
      500,
      'INTERNAL_ERROR'
    );
  }
}

/**
 * PATCH /api/v1/webhooks/[webhookId]
 * 
 * Update webhook configuration
 */
export async function PATCH(
  request: NextRequest,
  { params }: { params: Promise<{ webhookId: string }> }
) {
  const { webhookId } = await params; // Next.js 15+ requires awaiting params
  const ctx = await extractRequestContext(request, `/api/v1/webhooks/${webhookId}`);
  
  try {
    // Require authentication
    const authResult = await requireAuth(request, 'user');
    if ('response' in authResult) {
      return wrapResponse(authResult.response, ctx);
    }
    
    ctx.apiKeyId = authResult.apiKey.id;
    
    const config = await getWebhookConfig(webhookId);
    if (!config) {
      return NextResponse.json(
        {
          error: 'Webhook not found',
          code: 'NOT_FOUND',
        },
        { status: 404 }
      );
    }
    
    const body = await request.json();
    const updates: Partial<typeof config> = {};
    
    if (body.url !== undefined) {
      try {
        new URL(body.url);
        updates.url = body.url;
      } catch {
        return NextResponse.json(
          {
            error: 'Invalid URL',
            code: 'INVALID_URL',
          },
          { status: 400 }
        );
      }
    }
    
    if (body.events !== undefined) {
      updates.events = body.events as WebhookEventType[];
    }
    
    if (body.enabled !== undefined) {
      updates.enabled = body.enabled;
    }
    
    const updatedConfig = {
      ...config,
      ...updates,
      updatedAt: new Date().toISOString(),
    };
    
    await setWebhookConfig({
      ...updatedConfig,
      apiKeyId: authResult.apiKey.id,
    });
    
    // Return config without secret
    return NextResponse.json({
      id: updatedConfig.id,
      endpointId: updatedConfig.endpointId,
      url: updatedConfig.url,
      events: updatedConfig.events,
      enabled: updatedConfig.enabled,
      createdAt: updatedConfig.createdAt,
      updatedAt: updatedConfig.updatedAt,
    });
  } catch (error) {
    return createErrorResponse(
      ctx,
      error instanceof Error ? error : new Error('Internal server error'),
      500,
      'INTERNAL_ERROR'
    );
  }
}

